import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import SingleItem from '../SingleItem';

const WithdrawalsLog = () => {

    const [dataSum, setdataSum] = useState([]);
    const [data, setData] = useState([]);
    useEffect(() => {
        fetch(`http://66.29.142.198:5000/api/admin/withdrawal/all/view`, {
            method: 'GET',
        })
            .then((res) => res.json())
            .then((data) => {
                setData(data.data)
                setdataSum(data)
            })
    }, [])

    return (
        <>
            <div class="d-flex mb-30 flex-wrap gap-3 justify-content-between align-items-center">
                <h6 class="page-title">Withdrawals Log</h6>
                <div class="d-flex flex-wrap justify-content-end gap-2 align-items-center breadcrumb-plugins">
                    <form action="" method="GET" class="d-flex flex-wrap gap-2 search-form">
                        <div class="input-group w-auto flex-fill">
                            <input type="search" name="search" class="form-control bg--white" placeholder="Search..." value="" id="search" />
                            <button class="btn btn--primary" type="submit"><i class="la la-search"></i></button>
                        </div>
                        <div class="input-group w-auto flex-fill">
                            <input name="date" type="search" data-range="true" data-multiple-dates-separator=" - " data-language="en" data-format="Y-m-d" class="datepicker-here form-control bg--white pe-2" data-position="bottom right" placeholder="Start Date - End Date" autocomplete="off" value="" id="date" />
                            <button class="btn btn--primary input-group-text"><i class="la la-search"></i></button>
                        </div>



                    </form>
                </div>
            </div>

            <div class="row justify-content-center">
                <div class="col-xl-4 col-sm-6 mb-30">
                    <div class="widget-two box--shadow2 has-link b-radius--5 bg--success">
                        <Link href="https://gffexvip.biz/admin/withdraw/approved" class="item-link"></Link>
                        <div class="widget-two__content">
                            <h2 class="text-white">${dataSum.WithdrawalAcceptSum}</h2>
                            <p class="text-white">Approved Withdrawals</p>
                        </div>
                    </div>
                </div>
                <div class="col-xl-4 col-sm-6 mb-30">
                    <div class="widget-two box--shadow2 has-link b-radius--5 bg--6">
                        <Link href="https://gffexvip.biz/admin/withdraw/pending" class="item-link"></Link>
                        <div class="widget-two__content">
                            <h2 class="text-white">${dataSum.WithdrawalPendingSum}</h2>
                            <p class="text-white">Pending Withdrawals</p>
                        </div>
                    </div>
                </div>
                <div class="col-xl-4 col-sm-6 mb-30">
                    <div class="widget-two box--shadow2 b-radius--5 has-link bg--pink">
                        <Link href="https://gffexvip.biz/admin/withdraw/rejected" class="item-link"></Link>
                        <div class="widget-two__content">
                            <h2 class="text-white">${dataSum.WithdrawalRejectSum}</h2>
                            <p class="text-white">Rejected Withdrawals</p>
                        </div>
                    </div>
                </div>
                <div class="col-lg-12">
                    <div class="card b-radius-10 ">
                        <div class="card-body p-0">

                            <div class="table-responsive-sm table-responsive">
                                <table class="table table-light style-two mb-0">
                                    <thead>
                                        <tr>
                                            <th>Gateway | Transaction</th>
                                            <th>Initiated</th>
                                            <th>User</th>
                                            <th>Amount</th>
                                            <th>Conversion</th>
                                            <th>Status</th>
                                            <th>Action</th>

                                        </tr>
                                    </thead>
                                    <tbody>
                                    {data.length !== 0 ?
                                            data.map((data, index) => {
                                                if (data) {
                                                    return (
                                                        <SingleItem data={data} index={index} key={data._id} ></SingleItem>
                                                    );
                                                }
                                            }) :
                                            <tr>
                                                <td className="text-muted text-center" colspan="100%">Data not found</td>
                                            </tr>}
                                    

                                    </tbody>
                                </table>
                            </div>
                        </div>
                        {/* <div class="card-footer py-4">
                            <nav class="d-flex justify-content-end">
                                <ul class="pagination">

                                    <li class="page-item disabled" aria-disabled="true" aria-label=" Previous">
                                        <span class="page-link" aria-hidden="true"></span>
                                    </li>





                                    <li class="page-item active" aria-current="page"><span class="page-link">1</span></li>
                                    <li class="page-item"><a class="page-link" href="https://gffexvip.biz/admin/withdraw/log?page=2">2</a></li>
                                    <li class="page-item"><a class="page-link" href="https://gffexvip.biz/admin/withdraw/log?page=3">3</a></li>
                                    <li class="page-item"><a class="page-link" href="https://gffexvip.biz/admin/withdraw/log?page=4">4</a></li>


                                    <li class="page-item">
                                        <a class="page-link" href="https://gffexvip.biz/admin/withdraw/log?page=2" rel="next" aria-label="Next "></a>
                                    </li>
                                </ul>
                            </nav>

                        </div> */}
                    </div>
                </div>
            </div>
        </>
    );
};

export default WithdrawalsLog;