import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import dateFormat from "dateformat";

const Details = () => {
    const { id } = useParams();
    const [data, setData] = useState([]);
    useEffect(() => {
        fetch(`http://66.29.142.198:5000/api/admin/user/view/single/${id}`, {
            method: 'GET',
        })
            .then((res) => res.json())
            .then((data) => {
                setData(data.data)
            })
    }, [])

    return (
        <>
            <div class="d-flex mb-30 flex-wrap gap-3 justify-content-between align-items-center">
                <h6 class="page-title">Details</h6>
                <div class="d-flex flex-wrap justify-content-end gap-2 align-items-center breadcrumb-plugins">
                </div>
            </div>

            <div class="row mb-none-30 justify-content-center">
                <div class="col-xl-12 col-md-6 mb-30">
                    <div class="card b-radius--10 overflow-hidden box--shadow1">
                        <div class="card-body">
                            <h5 class="mb-20 text-muted">Details</h5>
                            <ul class="list-group">
                                <li class="list-group-item d-flex justify-content-between align-items-center">
                                    Name                            <span class="fw-bold">{data?.name}</span>
                                </li>
                                <li class="list-group-item d-flex justify-content-between align-items-center">
                                    Email                            <span class="fw-bold"> {data?.email}</span>
                                </li>
                                <li class="list-group-item d-flex justify-content-between align-items-center">
                                   Password                          <span class="fw-bold">
                                   {data?.password}
                                    </span>
                                </li>
                                <li class="list-group-item d-flex justify-content-between align-items-center">
                                    Join                           
                                     <span class="fw-bold"> {dateFormat(data?.created_at, "d-m-yyyy h:MM:ss TT")}</span>
                                </li>
                              
                                <li class="list-group-item d-flex justify-content-between align-items-center">
                                    Status

                                    {data?.status === 1 ?
                                        <span class="badge badge--warning text-warning">Pending</span>
                                        : ''}
                                    {data?.status === 0 ?
                                    <span class="fw-bold"><span class="badge badge--success text-success">Active</span></span>
                                        
                                        : ''}
                                    {data?.status === 2 ?
                                        <span class="badge badge--danger text-danger">Rejected</span>
                                        : ''}

                                    <span>
                                        <br />
                                        {/* 1 week ago */}
                                    </span>                        </li>
                            </ul>
                        </div>
                    </div>
                </div>
              
            </div>
        </>
    );
};


export default Details;